import "./consiglio.js";
import { Paginazione } from "../../commons/js/render_paginazione.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";

$(document).ready(function () {

    var DtoFiltro = function () {
        this.oggetto = { label: 'oggetto', value: null };
        this.title = { label: 'cig', value: null };
        this.procedura_di_scelta = { label: 'procedura', value: null };
        this.data_di_pubblicazione_start = { label: 'dataDal', value: null };
        this.data_di_pubblicazione_end = { label: 'dataAl', value: null };
        this.struttura_proponente = { label: 'struttura', value: null };
    };

    function visualizzaElencoFile(elenco) {
        let html = '<ul class="list-unstyled">';
        elenco.components.forEach(item => {
            let url = apibase.baseurl + item.properties.address;
            html += '<a href="' + url + '" target="_blank">' + item.title + '</a>'
        })
        html += '</ul>';
        return html;
    }

    function visualizzaData(val) {
        let date = new Date(val);
        return StringRenderer.renderData(date, "-");
    }

    function visualizzaImporto(val) {
        return StringRenderer.renderValuta(val);
    }

    const modelloTabella = [
        { label: "CIG", tipoCampo: "property", campo: "title" },
        { label: "Struttura Proponente", tipoCampo: "property", campo: "struttura_proponente" },
        { label: "Oggetto del bando", tipoCampo: "property", campo: "oggetto" },
        { label: "Procedura di scelta del contraente", tipoCampo: "property", campo: "procedura_di_scelta" },
        { label: "Elenco degli operatori invitati a presentare offerte", tipoCampo: "component", campo: "elenco_operatori_invitati", visualizzaCon: visualizzaElencoFile },
        { label: "Data di pubblicazione e/o affidamento", tipoCampo: "property", campo: "data_di_pubblicazione", visualizzaCon: visualizzaData },
        { label: "Data di scadenza", tipoCampo: "property", campo: "data_di_scadenza", optional: true, visualizzaCon: visualizzaData },
        { label: "Aggiudicatario", tipoCampo: "property", campo: "aggiudicatario" },
        { label: "Importo di aggiudicazione", tipoCampo: "property", campo: "importo_di_aggiudicazione_al_netto", visualizzaCon: visualizzaImporto },
        { label: "Data di effettivo inizio dei lavori o forniture", tipoCampo: "property", campo: "data_di_inizio_lavori", optional: true, visualizzaCon: visualizzaData },
        { label: "Tempi di completamento dell’opera, servizio o fornitura", tipoCampo: "property", campo: "data_di_ultimazione_lavori", visualizzaCon: visualizzaData },
        { label: "Importo delle somme liquidate", tipoCampo: "property", campo: "importo_delle_somme_liquidate", visualizzaCon: visualizzaImporto }
        // { label: "Documentazione relativa alle procedure", tipoCampo: "property", campo: "aggiudicatario", optional: true },
        // { label: "Data ultimo aggiornamento", tipoCampo: "field", campo: "lastModifiedDate", optional: true, visualizzaCon: visualizzaData }
    ];

    function renderColonna(datiColonna) {
        let riga = '';
        for (let colonna of colonne) {
            let valore = null;
            if (colonna.tipoCampo === "property") {
                valore = datiColonna.properties[colonna.campo];
            } else if (colonna.tipoCampo === "component") {
                valore = datiColonna.components[colonna.campo];
            } else {
                valore = datiColonna[colonna.campo];
            }
            if (valore) {
                if (colonna.visualizzaCon) {
                    valore = colonna.visualizzaCon(valore);
                }
            }
            riga += "<td>" + (valore || '') + "</td>";
        }
        return riga;
    }

    function renderCompleto(result) {
        let intestazioneTabella = '<tr>';
        colonne = modelloTabella;
        for (var colonna of colonne) {
            intestazioneTabella += "<th>" + colonna.label + "</th>";
        }
        intestazioneTabella += "</tr>"
        $("#thead").html(intestazioneTabella);
        const tabella = $("#tbody");
        if (result && result.content) {
            result.content.forEach(function (single) {
                const oid = single.id;
                tabella.append('<tr data-oid="' + oid + '"></tr>');
                $.ajax({
                    url: apibase.baseurl + "10300/" + oid,
                    success: function (result) {
                        if (result) {
                            const datiColonna = renderColonna(result);
                            $('#tbody tr[data-oid="' + result.id + '"]').first().html(datiColonna);
                        }
                    },
                });
            });
        }
        if (result.totalPages == 0) {
            $("#tbody").html('<tr><td colspan="' + colonne.length + '">Nessun risultato corrispondente alla ricerca</td></tr>');
        } else {
            $('#paginazione').html(Paginazione.renderizzaComponente(result, {
                paginationWrapper: 'justify-content-center'
            }));
        }
    }

    function creaData(stringaData) {
        const componentiData = stringaData.split('-').reverse();
        return componentiData.join('-');
    }

    $('#bottoneReset').click(function () {
        const vistaFiltro = new DtoFiltro();
        let query = window.location.search;
        for (var item of Object.values(vistaFiltro)) {
            query = query.replace(new RegExp('&' + item.label + '=[^&]*', 'g'), '');
        }
        window.location.search = query;
    });

    function submit() {
        const vistaFiltro = new DtoFiltro();
        //vistaFiltro.statoDaFiltrare = getStatoDaFiltrare();
        vistaFiltro.title.value = $('#cig').val();
        vistaFiltro.oggetto.value = $('#oggetto').val();
        vistaFiltro.procedura_di_scelta.value = $('#procedura').val();
        vistaFiltro.data_di_pubblicazione_start.value = $('#dataDal').val();
        vistaFiltro.data_di_pubblicazione_end.value = $('#dataAl').val();
        vistaFiltro.struttura_proponente.value = $('#struttura').val();
        let query = window.location.search;
        for (var item of Object.values(vistaFiltro)) {
            query = query.replace(new RegExp('&?' + item.label + '=[^&]*', 'g'), '');
            if (item.value != null && item.value != "") {
                query += '&' + item.label + '=' + item.value;
            }
        }
        window.location.search = query;
    }

    function submitTastoInvio(e) {
        if (e.which == 13) {
            submit();
        }
    }

    $('#bottoneCerca').click(submit);
    $('#oggetto').keypress(submitTastoInvio);
    $('#cig').keypress(submitTastoInvio);

    function estrapolaProcedura(key) {
        for (const procedura of procedure) {
            if (procedura.id == key) {
                return procedura;
            }
        }
    }

    function estrapolaStruttura(key) {
        for (const struttura of strutture) {
            if (struttura.id == key) {
                return struttura;
            }
        }
    }

    function estrapolaFiltro() {
        const filtro = {};
        let cig = urlParams.get('cig');
        if (cig != null) {
            filtro['title'] = cig;
            $('#cig').val(cig).change();
        }
        let oggetto = urlParams.get('oggetto');
        if (oggetto != null) {
            filtro['oggetto'] = oggetto;
            $('#oggetto').val(oggetto).change();
        }
        let dataDal = urlParams.get('dataDal');
        if (dataDal != null) {
            filtro['data_di_pubblicazione_start'] = creaData(dataDal);
            $('#dataDal').val(dataDal).change();
        }
        let dataAl = urlParams.get('dataAl');
        if (dataAl != null) {
            filtro['data_di_pubblicazione_end'] = creaData(dataAl);
            $('#dataAl').val(dataAl).change();
        }
        let proceduraDiScelta = urlParams.get('procedura');
        if (proceduraDiScelta != null) {
            const procedura = estrapolaProcedura(proceduraDiScelta);
            if (procedura) {
                filtro['procedura_di_scelta'] = procedura.id;
                $('#procedura').val(procedura.id + '').change();
            }
        }
        let strutturaProponente = urlParams.get('struttura');
        if (strutturaProponente != null) {
            const struttura = estrapolaStruttura(strutturaProponente);
            if (struttura) {
                filtro['struttura_proponente'] = struttura.id;
                $('#struttura').val(struttura.id + '').change();
            }
        }
        return filtro;
    }

    $('.it-date-datepicker').datepicker({
        inputFormat: ["dd-MM-yyyy"],
        outputFormat: 'dd-MM-yyyy',
    });

    var urlParams = new URLSearchParams(window.location.search);
    let colonne = modelloTabella;
    let strutture = [];
    let procedure = [];
    const procedure$ = new Promise(function (resolve, reject) {
        $.ajax({
            url: apibase.baseurl + '10302?size=50',
            success: function (resultProcedure) {
                if (resultProcedure && resultProcedure.content) {
                    procedure = resultProcedure.content;
                }
                resolve();
            },
            error: reject
        });
    })

    const strutture$ = new Promise(function (resolve, reject) {
        $.ajax({
            url: apibase.baseurl + '10301?size=50',
            success: function (resultStrutture) {
                if (resultStrutture && resultStrutture.content) {
                    strutture = resultStrutture.content;
                }
                resolve();
            },
            error: reject
        })
    });

    function initSelectProcedure() {
        const selectProcedura = $("#procedura");
        procedure.forEach(function (procedura) {
            selectProcedura.append('<option value="' + procedura.id + '">' + procedura.title + '</option>');
        });
        selectProcedura.selectpicker('refresh');
    }

    function initSelectStrutture() {
        const selectStruttura = $("#struttura");
        strutture.forEach(function (struttura) {
            selectStruttura.append('<option value="' + struttura.id + '">' + struttura.title + '</option>');
        });
        selectStruttura.selectpicker('refresh');
    }

    Promise.all([procedure$, strutture$]).then(function () {
        var url_to_call = apibase.baseurl + "10300?size=20";
        url_to_call += Paginazione.inizializzaUrl(urlParams);
        initSelectProcedure();
        initSelectStrutture();
        const filtro = estrapolaFiltro();
        $.ajax({
            method: 'POST',
            url: url_to_call,
            data: JSON.stringify(filtro || {}),
            success: renderCompleto,
            contentType: 'application/json',
            dataType: 'json'
        });

        $.ajax({
            method: 'POST',
            url: apibase.baseurl + 'dda/10300',
            success: function (risposta) {
                const data = new Date(risposta);
                const dataFormattata = StringRenderer.renderData(data, '/');
                $('#dataUltimoAggiornamento').html(dataFormattata);
            }
        });
    });





});